/* eslint-disable @typescript-eslint/consistent-type-definitions */
import * as React from 'react';
import { Controller } from '@ali/4ever-cangjie';
import ItemView from '../buttons';
import { DIVIDER, NEW_LINE, DIVIDER_NONE } from '../constant';
import { MobileToolbarGroup, MobileToolbarItem, Plugins } from '../types';

import { GroupWrapper, ContentWrapper, SectionDivider } from './styled';

type CommonProps = {
  controller: Controller;
  plugins: Plugins;
};

type RowProps = CommonProps & {
  items: MobileToolbarItem[];
  borderless?: boolean;
};

type IProps = CommonProps & {
  layout: MobileToolbarGroup[];
};

function Group({ items, controller, plugins, borderless }: RowProps) {
  return (
    <GroupWrapper borderless={borderless}>
      {items.map((item, index) => {
        const isStart =
          index === 0 ||
          items[index - 1] === DIVIDER ||
          items[index - 1] === DIVIDER_NONE ||
          items[index - 1] === NEW_LINE;
        const isEnd =
          index === items.length - 1 ||
          items[index + 1] === DIVIDER ||
          items[index + 1] === DIVIDER_NONE ||
          items[index + 1] === NEW_LINE;
        return (
          <ItemView
            key={typeof item === 'string' ? `${item}_${index}` : item.name}
            item={item}
            isStart={isStart}
            isEnd={isEnd}
            controller={controller}
            plugins={plugins}
          />
        );
      })}
    </GroupWrapper>
  );
}

function LayoutPanel({ controller, plugins, layout }: IProps) {
  return (
    <ContentWrapper data-toolbar-panel-layout>
      {layout.map((group, index) => {
        return (
          <React.Fragment key={group.key}>
            {index !== 0 && <SectionDivider />}
            <Group
              items={group.items}
              borderless={group.borderless}
              controller={controller}
              plugins={plugins}
            />
          </React.Fragment>
        );
      })}
    </ContentWrapper>
  );
}

export default LayoutPanel;
